:root {
  --mainWhite: #f3f3f3;
  --mainGreen: #44d1ad;
  --mainBlack: #343a40;
  --mainPink: #fc0388; }

.App {
  position: relative;
  min-height: 100vh;
  margin: 0;
  background: #f3f3f3;
  background: var(--mainWhite);
  font-family: "Montserrat", sans-serif; }
  .App .heading,
  .App .sub-heading {
    font-family: "Calistoga", cursive; }

.landing-section-1 {
  position: relative; }
  .landing-section-1 .carousel-overlap {
    width: 100%;
    height: 500px;
    position: absolute;
    bottom: 0;
    right: 0;
    margin-bottom: 10px;
    padding: 5rem; }
    .landing-section-1 .carousel-overlap .left-side {
      color: white;
      font-size: 100px;
      background-color: transparent; }
    .landing-section-1 .carousel-overlap .right-side > .card-custom {
      position: absolute;
      right: 0;
      background-color: var(--mainWhite);
      width: 80%;
      height: 300px;
      padding: 2rem; }
      .landing-section-1 .carousel-overlap .right-side > .card-custom button.btn-sign-up {
        font-weight: bold;
        background-color: var(--mainGreen);
        color: var(--mainBlack);
        border: var(--mainGreen) solid 2px;
        padding: 10px 20px; }
        .landing-section-1 .carousel-overlap .right-side > .card-custom button.btn-sign-up:hover {
          text-decoration: none;
          font-weight: bolder; }
      .landing-section-1 .carousel-overlap .right-side > .card-custom button.btn-learn-more {
        font-weight: bold;
        color: var(--mainBlack);
        background-color: var(--mainWhite);
        border: var(--mainGreen) solid 2px;
        padding: 10px 20px; }
        .landing-section-1 .carousel-overlap .right-side > .card-custom button.btn-learn-more:hover {
          text-decoration: none;
          font-weight: bolder; }

.carousel-container {
  object-fit: cover;
  position: relative;
  width: 100%;
  height: 600px;
  opacity: 0.8; }
  .carousel-container .carousel-pic {
    position: absolute;
    object-fit: cover;
    width: 100%;
    height: 100%; }

@media screen and (max-width: 360px) {
  .carousel-container {
    height: 400px; } }

#content-wrap {
  padding-bottom: 15rem; }

/* Footer */
#footer {
  background-color: var(--mainBlack);
  color: white;
  text-align: center;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 15rem; }

div.footer-content {
  background-color: var(--mainBlack);
  text-align: left;
  padding: 4em; }
  div.footer-content .sub-head {
    color: var(--mainGreen);
    text-transform: uppercase;
    font-weight: bolder;
    margin-bottom: 1em; }
  div.footer-content ul.footer-links {
    list-style-type: none;
    padding: 0; }
    div.footer-content ul.footer-links li a.link {
      text-decoration: none;
      color: var(--mainWhite); }
      div.footer-content ul.footer-links li a.link:hover {
        text-decoration: none;
        color: var(--mainGreen); }
  div.footer-content .mini-form {
    font-family: "Roboto Condensed", sans-serif;
    text-align: center; }
    div.footer-content .mini-form .sub-head {
      font-family: 'Montserrat', sans-serif;
      text-align: left; }
    div.footer-content .mini-form .footer-icon {
      color: var(--mainGreen);
      font-size: 40px; }
    div.footer-content .mini-form .sub-heading-login {
      font-size: 16px; }
    div.footer-content .mini-form .content {
      text-align: left; }
      div.footer-content .mini-form .content a.sign-up-link {
        color: var(--mainGreen); }
      div.footer-content .mini-form .content .sign-up-qn {
        font-weight: bold; }
      div.footer-content .mini-form .content .btn-footer {
        width: 100%;
        text-transform: uppercase;
        font-size: 15px !important;
        font-weight: bold !important;
        padding: 5px 50px !important;
        background-color: var(--mainGreen) !important;
        color: var(--mainBlack) !important;
        border-radius: 0 !important;
        cursor: pointer !important; }
        div.footer-content .mini-form .content .btn-footer:focus {
          outline: none !important; }
  div.footer-content .sponsors {
    text-align: center; }
    div.footer-content .sponsors .sponsor-logo {
      margin-top: 20px;
      width: 80px;
      border-radius: 50px; }

@media screen and (max-width: 360px) {
  #footer {
    bottom: 0; }
  div.footer-content {
    font-size: 13px; }
    div.footer-content .sub-head {
      text-align: left; } }

body {
  font-size: 0.875rem; }

.feather {
  width: 16px;
  height: 16px;
  vertical-align: text-bottom; }

/*
 * Sidebar
 */
.sidebar {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  /* Behind the navbar */
  padding: 48px 0 0;
  /* Height of navbar */
  box-shadow: inset -1px 0 0 rgba(0, 0, 0, 0.1); }

.sidebar-sticky {
  position: relative;
  top: 0;
  height: calc(100vh - 48px);
  padding-top: 0.5rem;
  overflow-x: hidden;
  overflow-y: auto;
  /* Scrollable contents if viewport is shorter than content. */ }

@supports ((position: -webkit-sticky) or (position: sticky)) {
  .sidebar-sticky {
    position: -webkit-sticky;
    position: sticky; } }

.sidebar .nav-link {
  font-weight: 500;
  color: #999; }
  .sidebar .nav-link:hover, .sidebar .nav-link:focus {
    color: var(--mainGreen); }

.sidebar-heading {
  font-size: 0.75rem;
  text-transform: uppercase; }

/*
 * Content
 */
[role="main"] {
  padding-top: 48px;
  /* Space for fixed navbar */ }

/*
 * Navbar
 */
.navbar-brand {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  font-size: 1rem;
  background-color: rgba(0, 0, 0, 0.25);
  box-shadow: inset -1px 0 0 rgba(0, 0, 0, 0.25); }

.navbar .form-control {
  padding: 0.75rem 1rem;
  border-width: 0;
  border-radius: 0; }

.form-control-dark {
  color: #fff;
  background-color: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.1); }

.form-control-dark:focus {
  border-color: transparent;
  box-shadow: 0 0 0 3px rgba(255, 255, 255, 0.25); }

.logo {
  background-color: var(--mainBlack); }
  .logo span {
    font-weight: bolder; }

.login-button {
  color: white;
  background-color: var(--mainBlack);
  border: white solid 2px;
  padding: 10px 20px; }
  .login-button:hover {
    text-decoration: none;
    color: var(--mainBlack);
    background-color: white; }

.register-button {
  color: var(--mainGreen);
  background-color: var(--mainBlack);
  border: var(--mainGreen) solid 2px;
  padding: 10px 20px; }
  .register-button:hover {
    text-decoration: none;
    color: var(--mainBlack);
    background-color: var(--mainGreen); }

@media screen and (max-width: 770px) {
  .login-button, .register-button {
    border: none;
    color: var(--mainWhite);
    text-decoration: none; }
    .login-button:focus, .register-button:focus {
      text-decoration: none;
      color: var(--mainGreen);
      background-color: var(--mainBlack); } }

